.index-manage-row {
    display: flex;
    margin-left: -10px;
    margin-right: -10px;

    &:first-child {
        margin-bottom: 20px; }

    & > .grid-publication {
        margin-left: 10px;
        margin-right: 10px;

        &:not(.grid-publication_6-col) {
            flex-basis: 25%; } } }

.todo-layout {
    display: grid;
    grid-template-columns: repeat(4, 310px);
    grid-gap: 20px;

    & > .grid-publication_6-col {
        grid-column: 1/3;
        grid-row: 1/2; }

    & > .cinema-widget {
        grid-column: 4/5;
        grid-row: 1/2; } }
